/* Reset some default styles */
body, h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  color: #333;
}

/* Header styles */
#header {
  background: #222;
  color: #fff;
  padding: 20px 0;
}

#navbar ul {
  list-style: none;
  display: flex;
}

#navbar li {
  margin-right: 20px;
}

#navbar a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
}

/* About Section styles */
.about {
  padding: 60px 0;
}

.about-me img {
  max-width: 100%;
  height: auto;
}

/* Projects Section styles */
.projects {
  background-color: #f4f4f4;
  padding: 60px 0;
}

.icon-box {
  background: #fff;
  padding: 30px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
}

.icon-box:hover {
  transform: scale(1.05);
}

.icon-box i {
  font-size: 48px;
  color: #333;
}

.icon-box h4 {
  font-size: 24px;
  font-weight: bold;
  margin-top: 15px;
}

.icon-box p {
  font-size: 16px;
  color: #777;
}

/* Contact Section styles */
.contact {
  padding: 60px 0;
}

.info-box {
  background: #fff;
  padding: 30px;
  text-align: center;
}

.info-box i {
  font-size: 48px;
  color: #333;
}

.info-box h3 {
  font-size: 24px;
  font-weight: bold;
  margin-top: 15px;
}

.info-box p {
  font-size: 16px;
  color: #777;
}

/* Social Links styles */
.social-links a {
  margin-right: 15px;
  font-size: 24px;
  color: #333;
  transition: color 0.3s ease-in-out;
}

.social-links a:hover {
  color: #007bff;
}
